.contact-page {
  padding: 0 4em;
  margin: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.contact-page__header {
  color: #ff8832;
  font-size: 4em;
  padding: 1em;
}

.contact-page__form-wrapper {
  display: flex;
  gap: 9em;
  margin-top: 2em;
  padding: 4em;
  padding-right: 6em;
  border: 6px solid black;
  border-radius: 30px;
  font-family: "Montserrat", sans-serif;
}

.contact-page__form-left {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4em;
  min-height: 100%;
}

.contact-page__form {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8em;
  width: 500px;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5em;
  width: 100%;
}

.form-group__label {
  font-size: 1.1em;
  font-family: "Montserrat", sans-serif;
}

.form-group__input {
  border: 1px solid black;
  border-radius: 10px;
  padding: 1.5em;
  font-size: 1em;
}

.form-group__textarea {
  border: 1px solid black;
  border-radius: 10px;
  padding: 1.5em;
  font-size: 1em;
  height: 140px;
}

.contact-page__form__submit {
  background: #ff8832;
  color: white;
  padding: 1em;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 1.4em;
  border: none;
  border-radius: 30px;
  width: 100%;
  margin-top: 0.5em;
}

@media screen and (max-width: 1024px) {
  .contact-page__form-wrapper {
    display: flex;
    gap: 6em;
    margin-top: 2em;
    padding: 2em 3em;
  }

  .contact-page__form {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8em;
    width: 400px;
  }
}

@media screen and (max-width: 900px) {
  .contact-page__form-wrapper {
    display: flex;
    gap: 3em;
    margin-top: 2em;
    padding: 2em 3em;
  }

  .contact-page__form-left__logo {
    width: 160px;
  }

  .contact-image {
    width: 160px;
  }

  .contact-page__form {
    gap: 0.8em;
    width: 280px;
  }

  .form-group__label {
    font-size: 0.8em;
  }

  .form-group__input {
    padding: 0.75em;
    font-size: 1em;
  }
  .form-group__textarea {
    padding: 0.75em;
    font-size: 1em;
    height: 100px;
  }
  .contact-page__form__submit {
    padding: 1.2em;
    font-size: 0.8em;
  }
}

@media screen and (max-width: 600px) {
  .contact-page {
    padding: 2em 2em;
    margin: 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .contact-page__header {
    color: #ff8832;
    font-family:  sans-serif;
    font-size: 2.8em;
    padding: 1em 0;
  }
  .contact-page__form-wrapper {
    flex-direction: column;
    gap: 2em;
    margin-top: 2em;
    padding: 2em 1.5em;
  }

  .contact-page__form-left {
    flex-direction: row;
    align-items: center;
    gap: 2em;
    min-height: 100%;
  }

  .contact-page__form-left__logo {
    width: 160px;
  }

  .contact-image {
    width: 80px;
  }

  .contact-page__form {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5em;
    width: 100%;
  }
  .form-group {
    display: flex;
    flex-direction: column;
    gap: 0.2em;
    width: 100%;
  }

  .form-group__label {
    font-size: 1em;
    font-family: "Montserrat", sans-serif;
  }

  .form-group__input {
    padding: 1em;
    font-size: 1em;
  }

  .form-group__textarea {
    border: 1px solid black;
    border-radius: 10px;
    padding: 1em;
    font-size: 1em;
    height: 140px;
  }

  .contact-page__form__submit {
    padding: 1em;
    font-size: 1em;
  }
}
